// import s from './BarCard.module.scss';
import {Row, Col, Image, Collapse} from 'antd';

export const BarCard = ({ barItem }) => {
  return (
          <Col span={24} md={12} lg={8} xl={6} key={barItem.id}>
            <Row>
              <Col span={24}>
                <Image
                  src={barItem.image}
                  preview={false}
                  width={'100%'}
                  fallback="data:image/png;base64,"
                />
              </Col>
              <Col span={24}>
                <p>{barItem.name}</p>
              </Col>
              <Col span={24}>
                <p>{barItem.description}</p>
              </Col>
              <Col span={24}>
                <Collapse
                  items={[
                    { key: '1',
                      label: 'Recipe',
                      children: <>{barItem.recipe.map((recipeItem, index)=>(
                        <p key={index}>
                          <span>{recipeItem.ingredient}</span>
                          {
                            recipeItem.amount && <span>: {recipeItem.amount}</span>
                          }
                        </p>
                      ))}</>
                    },
                    { key: '2',
                      label: 'History',
                      children: <p>{barItem.history}</p>
                    }]}
                />
              </Col>
            </Row>
          </Col>
  );
};

